.details_card{
     display: flex;
     flex-direction: row;
     padding: 4px 20px;
     box-shadow: 13px 13px 0px 0px  #FFFFFF;
     }

@media screen and (max-width: 900px)
{
    .details_card{
        flex-direction: column;
        padding:4px 3px ;
    }
}
.upper{
    text-align: center;
    margin: 10px;
}
.upper_heading{
    font-weight: 600;
    font-size: 219%;
    color:#0066FF;
}
.lower_heading{
    font-weight: 400;
    font-size: 90%;
    color: #6c6c6c;
}