
.card{
    padding-top: 10px;
    width: 100%;
    display: flex;
    margin-top: 23px;
    margin-left: 10px;
    margin-right: 10px;
}

.card_imageContainer{
    padding: 5px 10px ;
}

.upper_image>img{
    z-index: 10;
    width: 100%;
    object-fit: contain;
    height: auto;
}
.icon_image>img{
    top: -50px;
    z-index: 30;
    width: 100px;
    height: auto;
    border-radius: 50%;
    display: block;
  margin-left: auto;
  margin-right: auto;
}
.icon_image{
    position: relative;
    top: -50px;
    
}
.card_content{
    padding: 5px 10px;
}

.card_content>h3{
    font-size: 2vw;
text-align: center;
text-transform: uppercase;
}
.card_content>h3:hover{
    font-weight: 700;
    color: blue;
}
.component_description{
    margin-top: 20px;
    line-height: 22px;
}

.upper_image:hover{
   position: relative;
   top:-15px;
  

}
@media screen and (max-width: 900px)
{

    .card_content>h3{
        font-size: 19px;
    }
    .component_description>p{
        font-size: 11px;
    }
}