.future{
    width: 90%;
    position: relative;
    margin: 10px;
    height: auto;
    padding: 2px;
    transition: left;
  }
  .future_inner{
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
  }
  .future_heading{
      color:#2172FF;
      text-align: center;
      font-size: 120%;
      margin: 12px;
      font-weight: 700;
  }
  .future_description_list{
      font-weight: 300;
  }
  .future_description_list li{
      display: list-item;
      text-align: -webkit-match-parent;
      font-family: 'PT Sans',sans-serif;
      font-size: 13px;
      line-height: 22px;
      color: #6c6c6c
  }