.header{
    background-color: #FFFFFF;
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    position: sticky;
    top: 0;
    z-index: 100;
    max-height: 250px;
    width: 100%;

}
.logo{
    margin-left: 30px;
    margin-top: 15px;
}

@media screen and (max-width: 700px) {
    .header_content{
        display: none;
    }
    
   .res{
       height: 230px;
   }
    .responsive{
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 30px;
         width: 100%;
        margin-top: 20px;
        margin-bottom: 30px;
    }
    .header_item{
    

        color: black;
        font-size: 17px;
        display: block;
        padding: 3px 0px 0px 30px;
        
    }
    .header_item:hover{
        text-decoration: none;
        color: blue;
        background-color: burlywood;
    }
    .icon{
        margin-right: 30px;
        margin-top: 15px;
    }
    
  }
  
  @media screen and (min-width: 700px) {
    .header_content{
        display: flex;
        margin-top: 14px;
    }
    .icon{
        display: none;
    }
    .header_item{
    
        padding: 3px 15px;
        color: black;
        font-size: 17px;
        
    }
    .header_item:hover{
        text-decoration: none;
        color: blue;
    }
}
  