.testimonial{
    background-image: url("https://engineeringhometuition.com/wp-content/uploads/2015/02/shutterstock_186067706.jpg");
    background-repeat: no-repeat;
    background-color: transparent;
    background-attachment: fixed;
    background-position: center 0;
    color: #FFFFFF;
    padding: 15px 20px;
    background-size: cover;
    max-height: 700px;

}
.title{
    margin-top: 24px;
    margin-bottom: 30px;
    text-align: center;
}
.title >h1{
    /* font-size: 25px; */
    font-size: 3.5vw;
}
.title>h3{
    color: #ffffff;
    font-size: 1.5vw;
  font-weight: 400;
  font-family: 'Oswald',sans-serif;
  line-height: 46px;
}
.testimonial_item{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 30px;
    flex-wrap: wrap;

}
.achivement{
    flex: 0.7;
}
.count_up{
    flex: 0.3;
    font-size: 55px;
    font-weight: 600;
    color: #FFFFFF;
    display: flex;
    flex-direction: column;
    align-items: center;
}
@media screen and (max-width: 650px) {
    .achivement{
        width: 100%;
    }
    .testimonial_item{
        flex-direction: column;
    }
    .title{
        margin-top: 19px;
        margin-bottom: 15px;
    }
    .title>h1{
        font-size: 20px;
    }
    .title>h3{
        font-size: 11px;
      font-weight: 400;
      font-family: 'Oswald',sans-serif;
      line-height: 19px;
    }
    .count_up{
  font-size: 35px;
  margin-top: 14px;
    }
  }