.footer{
    /* margin-top:px; */
    height: fit-content;
    width: 100%;
    color:white;
    padding: 50px;
    display: flex;
    justify-content: space-around;
    background-color: #232323;
}
.footer div{
    margin-top: 0px;
    display: flex;
    border-left:1px solid #232323;
    flex-direction: column;
    width:22vw;
    border-left :1px solid #6f6f6f;
    padding-left: 15px;

}
.footer div span{
    padding: 10px;
    color:#8f8f8f;
    width: 100%;
}
.footer div span:hover{
    font-weight: 700;
    color:white;
    cursor: pointer;
}
.footer div h{
    font-weight: 700;
    margin-bottom: 10px;
    font-size: 24px;
}
.footer_search input{
    display: inline-block;
    text-decoration: none; 
    border: 1px solid #e0dede;
     /* border-right: 0;  */
    outline: 0;
    color: #6c6c6c;
    background-color: transparent;
    font-family: "Oswald",sans-serif;
    font-size: 13px;
    margin: 0;
    padding: 2px 4%;
    width: 76%;
    height: 31px;
    line-height: 31px;
     
}
.footer_contact{
    padding: 10px 0px 0px 0px;
}

@media screen and (max-width: 700px)  {
    .footer{
        flex-direction: column;
        
    }
    .footer>div{
        width: 90%;
        border-left :1px solid #6f6f6f;
        padding-left: 14px;

    }
    .footer_search{
        height: 200px;
    }
    .footer_search >h{
        margin-top: 20px;
    }
    .footer_search input{
        width: 94%;
    }
}