.achievement{
    
  color: #FFFFFF;
  width: 70%;
  position: relative;
  margin: auto auto;
}

.achievement >h3{
  font-size: 16px;
  font-weight: 400;
  font-family: 'Oswald',sans-serif;
  line-height: 46px;
}
@media screen and (max-width: 650px) {
  .achievement{
    width: 89%;
  }
  .achievement>h3{
    font-size: 12px;
    line-height: 30px;
  }
}